@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
	margin: 0;
	padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

$dark: rgb(33, 37, 41);
$gray: #c4ccde;
$anchosidebar: 220px;
$bckgray: #6C76A4;

.navbar {
	box-shadow: 0px -10px 20px 0px rgba(18,18,23,1) inset;
	display:flex;
	justify-content: space-between;
	background-color: $bckgray;
	padding: 0px;
	color: $gray;
	height: 52px;
	width: 100vw;
	font-weight: 600;
}

// .logonavbarcontainer {
// 	// width: 220px;
// 	background-color: $dark;
// }

.logonavbar {
	height: 52px;
}

.flex {
	display: flex;
}

.sidebar {

	// box-shadow: -30px -30px 50px 15px rgba(18,18,23,1) inset;
	width: 220px;
	height: calc(100vh - 52px);
	background-color: $dark;
	ul {
		padding-top: 1rem;
		padding-left: 0;
		list-style: none;
		li {
			padding-bottom: 0.6rem;

			a {
				font-size: 1.2rem;
				font-weight: 600;
				text-decoration: none;
				color: $gray;
				&.active {
					background-color: $bckgray;

				}
			}
		}

	}
}

.icons {
	font-size: 1.3em;
	font-weight: bold;
	vertical-align: middle;
	color: $gray;


}

.primeralinea {
	padding-bottom: 2rem;
}

.contenido {
	background: linear-gradient(135deg, rgba(18,18,23,1) 30%, rgba(18,18,23,0.7) 60%, rgba(48,44,100,1) 100%);
	width: calc(100vw - 200px);
	padding: 10px 7px 10px 3px;
	box-shadow: -20px 0px 50px 30px rgba(18,18,23,1);
	// box-shadow: 0px 20px 50px 30px rgba(18,18,23,1);
}

h1 {// título de sección
	margin: 0;
	margin-left: 20px;
	text-align: left;
	font-family: "Audiowide", sans-serif;
	color: #0ccac4;
	font-weight: 200;
	text-shadow: 0px 0px 5px black;
}

.formulario {
	color: whitesmoke;
}
.signinboton {
	width: 100px;
}